import * as React from 'react'
import { Tree } from './Tree'
import { ButlerContext } from '@src/Root/contexts'
import { ITreeNode } from '@src/__generated__/backendSchemas'

export const CategoryPage: React.FC<{}> = (props) => {
  const butler = React.useContext(ButlerContext)
  const [root, setRoot] = React.useState<ITreeNode | null>(null)
  React.useEffect(() => {
    if (!root) {
      butler.TreefyCategories({}, 'replace', (data) => {
        setRoot(data.root)
      })
    }
  })
  if (!root) {
    return <div></div>
  }
  return (
    <Tree
      onSaveNode={(id: number, name: string) => butler.UpdateCategory({ id, name }, 'do-nothing')}
      onSelectIcon={(id: number, iconId: string) =>
        butler.UpdateCategory({ id, mui_icon_id: iconId }, 'do-nothing')
      }
      node={root}
    />
  )
}
