import { Button, Grid } from '@material-ui/core'
import * as React from 'react'
import { ImportForm } from './ImportForm'
import { NewStatementForm } from './NewStatementForm'

import { NewTransactionForm } from './NewTransactionForm'
import { TransactionListAppBar } from './TransactionListAppBar'
import { TransactionRows } from './TransactionRow'
import { useApolloQuery } from '@src/queries/__generated__/GetTransactions.hooks'
import { Timeline } from '@src/Root/Timeline'

type FormType = 'transaction' | 'plaid' | 'statement' | 'wave'

export const TransactionList = (props: {}) => {
  console.log('rerender entire list page')
  const [showTimeline, setShowTimeline] = React.useState(true)
  const [formType, setFormType] = React.useState<FormType | null>(null)

  const { loading, error, data, fetchMore, refetch } = useApolloQuery({
    variables: {},
  })
  if (error || !data) {
    console.log(error)
    return <div>error ...</div>
  }
  const loadMoreTransactions = () => {
    fetchMore({
      variables: { offset: data.transactions.length },
      updateQuery: (prev, { fetchMoreResult }) => {
        if (!fetchMoreResult) return prev
        return Object.assign({}, prev, {
          transactions: [...prev.transactions, ...fetchMoreResult.transactions],
        })
      },
    })
  }

  const showForm = (formType: FormType) => {
    setFormType(formType)
  }
  return (
    <div style={{ maxWidth: '100%' }}>
      <TransactionListAppBar
        toggleTimeline={() => setShowTimeline(true)}
        showForm={showForm}
        balance={data.balance}
      />
      <div
        style={{
          height: window.innerHeight - 70,
          overflow: 'scroll',
        }}
      >
        <Grid container>
          {showTimeline ? (
            <Grid item sm={3}>
              <Timeline
                onFilterEvent={(eventId: string) =>
                  refetch({
                    eventId,
                  })
                }
              />
            </Grid>
          ) : null}
          <Grid item sm={showTimeline ? 9 : 12}>
            <div style={{ marginBottom: '20px' }}>
              {formType === 'transaction' ? (
                <NewTransactionForm
                  selectableCategories={data.categories}
                  onClose={() => setFormType(null)}
                />
              ) : null}
              {formType === 'statement' ? (
                <NewStatementForm onClose={() => setFormType(null)} />
              ) : null}
              {formType === 'wave' ? <ImportForm onClose={() => setFormType(null)} /> : null}
            </div>
            {loading ? (
              <div>loading transactions ... </div>
            ) : (
              <TransactionRows categories={data.categories} transactions={data.transactions} />
            )}
            <Button fullWidth size="large" color="secondary" onClick={loadMoreTransactions}>
              ({data.transactions.length}) Click to Load More ...
            </Button>
          </Grid>
        </Grid>
      </div>
    </div>
  )
}
